   /* ==========================================================================
      Start of custom Webflow CSS
      ========================================================================== */

      .main{
        width:80%; 
        margin: auto;
      }

   .w-layout-grid {
     display: -ms-grid;
     display: grid;
     grid-auto-columns: 1fr;
     -ms-grid-columns: 1fr 1fr;
     grid-template-columns: 1fr 1fr;
     -ms-grid-rows: auto auto;
     grid-template-rows: auto auto;
     grid-row-gap: 16px;
     grid-column-gap: 16px;
   }
   
   body {
     background-color: #1a1a1a;
     font-family: Generalsans, sans-serif;
     color: #fff;
     font-size: 14px;
     line-height: 1.2;
   }
   
   h1 {
     margin-top: 20px;
     margin-bottom: 10px;
     font-size: 72px;
     line-height: 1.2;
     font-weight: 700;
   }
   
   h2 {
     margin-top: 20px;
     margin-bottom: 10px;
     font-size: 62px;
     line-height: 1.2;
     font-weight: 500;
   }
   
   h3 {
     margin-top: 20px;
     margin-bottom: 10px;
     font-size: 52px;
     line-height: 1.2;
     font-weight: 500;
   }
   
   h4 {
     margin-top: 10px;
     margin-bottom: 10px;
     font-size: 36px;
     line-height: 1.2;
     font-weight: 500;
   }
   
   h5 {
     margin-top: 10px;
     margin-bottom: 10px;
     font-size: 24px;
     line-height: 1.5;
     font-weight: 300;
   }
   
   h6 {
     margin-top: 10px;
     margin-bottom: 10px;
     font-size: 20px;
     line-height: 1.2;
     font-weight: 700;
   }
   
   p {
     margin-bottom: 10px;
     font-family: 'Source Code Pro', sans-serif;
     font-size: 20px;
     line-height: 1.4;
     letter-spacing: 0.7px;
   }
   
   a {
     -webkit-transition: color 350ms ease;
     transition: color 350ms ease;
     color: #fff;
     font-weight: 600;
     text-decoration: none;
   }
   
   a:hover {
     color: #ffd660;
   }
   
   ul {
     margin-top: 0px;
     margin-bottom: 10px;
     padding-left: 40px;
     font-size: 18px;
     font-weight: 500;
   }
   
   li {
     font-size: 16px;
     line-height: 1.6;
     font-weight: 500;
   }
   
   blockquote {
     margin-top: 50px;
     margin-bottom: 50px;
     padding: 50px 32px;
     border-left: 5px none #e2e2e2;
     background-image: url("https://assets.website-files.com/6319f69c5ef0f35ae3db6df0/632c1c272c8ac03d3a1531e5_Qoute%20Block%20Icon.svg"), url("https://assets.website-files.com/6319f69c5ef0f35ae3db6df0/632c1c272c8ac03d3a1531e5_Qoute%20Block%20Icon.svg");
     background-position: 97% 90%, 6px 10px;
     background-size: auto, auto;
     background-repeat: no-repeat, no-repeat;
     font-size: 24px;
     line-height: 1.2;
     font-style: italic;
     font-weight: 400;
     text-align: left;
   }

   #project-link{
    color: #000;
    }

  
    @media screen and (max-width:900px){
        .intro{
            flex-direction: column;
        }
        .intro div , #my-img{
                width: 100% !important;
        }   

        .small-circle {
            font-size: 10px !important;
            padding: 5px !important;
        }
     }

   ._20px-paragraph{
        font-family: 'Source Code Pro', sans-serif;
   }
   
  .project-holder{
    background-color: #030303;
    width: 100%;
    padding: 5%;
    }

    

   .section.black-section {
     position: relative;
     background-color: #030303;
   }
   
   .section.about-me-section {
     position: relative;
     margin-top: 120px;
     margin-bottom: 120px;
   }
   
   .section.overflow-hidden {
     position: relative;
     z-index: 1;
     overflow: hidden;
   }
   
   .container {
     max-width: 1170px;
     margin-right: auto;
     margin-left: auto;
     padding: 2em;
   }
   
   .container.nav-container {
     height: 100%;
     padding-top: 0em;
     padding-bottom: 0em;
   }
   
   .container._4ems {
     padding-top: 4em;
     padding-bottom: 4em;
   }
   
   .container.featured-container {
     position: relative;
     z-index: 1;
     padding-top: 8.9em;
     padding-bottom: 8.9em;
   }
   
   .container.blog-container {
     position: relative;
     z-index: 1;
     padding-top: 14em;
   }
   
   .container.hero {
     position: relative;
     z-index: 11;
     display: -webkit-box;
     display: -webkit-flex;
     display: -ms-flexbox;
     display: flex;
