     -ms-flex-pack: center;
     justify-content: center;
     -webkit-box-align: center;
     -webkit-align-items: center;
     -ms-flex-align: center;
     align-items: center;
   }
   
   .container.about-me-container {
     display: -webkit-box;
     display: -webkit-flex;
     display: -ms-flexbox;
     display: flex;
     width: 100%;
     padding-top: 0em;
     padding-bottom: 10em;
     -webkit-box-pack: justify;
     -webkit-justify-content: space-between;
     -ms-flex-pack: justify;
     justify-content: space-between;
     -webkit-box-align: center;
     -webkit-align-items: center;
     -ms-flex-align: center;
     align-items: center;
     grid-column-gap: 40px;
   }
   
   .container.contact {
     position: relative;
     z-index: 1;
     padding-top: 10.9em;
     padding-bottom: 10.9em;
   }
   
   .nav-menu-container {
     display: -webkit-box;
     display: -webkit-flex;
     display: -ms-flexbox;
     display: flex;
     height: 100%;
     -webkit-box-pack: justify;
     -webkit-justify-content: space-between;
     -ms-flex-pack: justify;
     justify-content: space-between;
     -webkit-box-align: center;
     -webkit-align-items: center;
     -ms-flex-align: center;
     align-items: center;
   }
   
   .navbar {
     position: -webkit-sticky;
     position: sticky;
     left: 0%;
     top: 0%;
     right: 0%;
     bottom: auto;
     z-index: 5000;
     height: 60px;
     background-color: #1a1a1a;
   }
   
   .nav-menu {
     display: -webkit-box;
     display: -webkit-flex;
     display: -ms-flexbox;
     display: flex;
     font-weight: 600;
   }
   
   .center-text {
     display: -webkit-box;
     display: -webkit-flex;
     display: -ms-flexbox;
     display: flex;
     -webkit-box-orient: vertical;
     -webkit-box-direction: normal;
     -webkit-flex-direction: column;
     -ms-flex-direction: column;
     flex-direction: column;
     -webkit-box-pack: start;
     -webkit-justify-content: flex-start;
     -ms-flex-pack: start;
     justify-content: flex-start;
     -webkit-box-align: center;
     -webkit-align-items: center;
     -ms-flex-align: center;
     align-items: center;
     text-align: center;
   }
   
   .top-margin-l {
     margin-top: 3em;
   }
   
   .button {
     padding: 22px 56px;
     border-radius: 158px;
     background-color: hsla(0, 0%, 85.1%, 0.4);
     -webkit-transition: color 400ms ease, background-color 500ms ease;
     transition: color 400ms ease, background-color 500ms ease;
     color: #fff;
     font-size: 24px;
     font-weight: 500;
   }
   
   .button:hover {
     background-color: #fff;
     color: #1d1d1d;
   }
   
   .button.big {
     padding: 20px 40px;
   }
   
   .grid-6-col {
     display: -ms-grid;
     display: grid;
     -webkit-box-flex: 1;
